import {Box, Container, Typography} from '@mui/material';
import { Helmet } from 'react-helmet';

const About = () => {
    return (
        <Box sx={{mt: '60px'}}>

            <Helmet>
                <meta
                    name="discription"
                    content="About"
                />
                <title>About</title>
            </Helmet>

            <Container>
                <Typography
                    textAlign={'center'}
                    mb={'30px'}
                >
                     Mui + React + Redux + React-Redux  
                     ,   React-router-dom, Formik 
                    ..
                </Typography>
                <Typography
                    sx={{textAlign: 'center'}}
                    mb={'30px'}
                    component={'a'}
                    href={'https://github.com/bodhiofflane'}
                >
                      Github
                </Typography>
            </Container>
        </Box>
    );
};

export default About;
